import React from 'react';
import { Navbar, Nav, Container, Button } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';

const Header = () => {
  return (
    <header>
      <Navbar bg="dark" variant="dark" expand="lg" collapseOnSelect>
        <Container>
          <LinkContainer to="/">
            <Navbar.Brand>Auction-Collector System</Navbar.Brand>
          </LinkContainer>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <LinkContainer to="/">
                <Nav.Link>Home</Nav.Link>
              </LinkContainer>
              <LinkContainer to="/items">
                <Nav.Link>Items</Nav.Link>
              </LinkContainer>
              <LinkContainer to="/collectors">
                <Nav.Link>Collectors</Nav.Link>
              </LinkContainer>
              <LinkContainer to="/auction">
                <Nav.Link>Auction</Nav.Link>
              </LinkContainer>
            </Nav>
            <Nav>
              <LinkContainer to="/upload">
                <Button variant="outline-light">Upload New Card</Button>
              </LinkContainer>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
};

export default Header;
